.App {
    .container {
      text-align: center;
      min-height: 100px;
      padding-bottom: 3em;
      .drop-region-container {
        margin-top: 3em;
        padding: 1em;
        border-radius: 10px;
        width: 80%;
        text-align: center;
        background-color: gray;
  
        .drop-region {
          pointer-events: none;
          border: dashed 2px white;
          #download-btn {
            filter: invert(1);
            margin-bottom: 30px;
          }
          #file-input {
            display: none;
          }
          color: white;
          border-radius: 10px;
          padding: 3em;
        }
      }
      .drop-region-container.highlight {
        background-color: limegreen;
      }
      .drop-region:hover {
        cursor: pointer;
      }
      .progress {
        margin-bottom: 2em;
        width: 78%;
        .progress-bar {
          width: 0%;
        }
      }
      #preview {
        margin-top: 20px;
        img {
          margin-top: 10px;
          margin-bottom: 10px;
          margin-left: auto;
          margin-right: auto;
          display: block;
          max-width: 50%;
        }
        button {
          max-width: 10em;
        }
      }
    }
  }
  